@import url(http://fonts.googleapis.com/css?family=Roboto+Slab|Open+Sans:400italic,700italic,400,700);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
:root{--color-black: #333333;--color-yellow: #FFE600;--color-blue: #3483FA;--color-white: #ffffff;--color-gray1: #999999;--color-gray2: #EEEEEE;--color-gray3: #666666}*{box-sizing:border-box}body{margin:0;font-family:"Roboto Slab",serif;font-weight:normal;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}h1,h2,h3,h4,h5,h6,li{font-family:"Open Sans",sans-serif;font-weight:normal}p{font-family:"Open Sans",sans-serif;font-weight:normal;color:#333333;color:var(--color-black)}.button{font-family:"Open Sans",sans-serif;font-weight:bold}a:link{text-decoration:none}a:visited{text-decoration:none}a:hover{text-decoration:none;color:#333333;color:var(--color-black)}a:active{text-decoration:none}

.row{display:flex;flex-wrap:wrap}.side{flex:20% 1;background-color:var(--color-black);padding:20px}main{background-color:var(--color-gray2);display:flex;align-items:center;justify-content:center}.wrapper{display:grid;grid-template-columns:1fr 1fr}.wrapper .container{border:1px solid var(--color-gray2);border-radius:5px;background-color:var(--color-white);padding:1em}.wrapper{border-radius:5px;background-color:var(--color-gray2)}.footer{padding:20px;text-align:center;background:var(--color-black)}@media screen and (max-width: 700px){.row{flex-direction:column}.wrapper{grid-template-columns:1fr}}@media screen and (max-width: 400px){.navbar a{float:none;width:100%}}

header{padding:10px;text-align:center;background:var(--color-yellow);color:var(--color-white)}header h1{font-size:40px}header .nav-logo{background-repeat:no-repeat;background-image:url(data:image/png;base64,);position:absolute;top:23px;left:150px;background-position:center;background-clip:border-box;transition:background-size 0.2s;width:106px;height:37px;text-decoration:none;cursor:pointer;overflow:hidden;text-indent:100%;white-space:nowrap}@media screen and (max-width: 1200px){header .nav-logo{left:10px}}


.SearchForm{text-align:left}.SearchForm .search-input{width:70%;padding:12px 20px;margin:8px 0;box-sizing:border-box;color:var(--color-gray1);font-size:18px}.SearchForm .search-input ::-webkit-input-placeholder{color:var(--color-gray3);opacity:1}.SearchForm .search-input :-ms-input-placeholder{color:var(--color-gray3);opacity:1}.SearchForm .search-input ::-ms-input-placeholder{color:var(--color-gray3);opacity:1}.SearchForm .search-input ::placeholder{color:var(--color-gray3);opacity:1}.SearchForm .nav-search-btn{background-repeat:no-repeat;background-image:url(data:image/png;base64,);background-position:center;background-clip:border-box;width:49px;height:49px;text-decoration:none;cursor:pointer;overflow:hidden;text-indent:126%;margin-left:-49px}


.Results{width:1184px;background-color:var(--color-white);border-radius:3px;border:solid 1px color var(--color-gray1);margin-top:16px}.Results .section{border-bottom:1px solid var(--color-gray2);padding:3px}.Results .section .detail-info{margin:30px 0 0 0}.Results .section .detail-info .title{margin:15px 0 0 0}.Results .section .detail-info .title h2{color:var(--color-gray1);font-size:20px}.Results .section .detail-info .price p{color:var(--color-gray3);font-size:25px}.Results .section .image-container{margin-left:10px}.Results .section .shipping{margin:50px 0 0 30px}.Results .section .shipping p{color:var(--color-gray3);font-size:15px}

.BreadCrumb{padding-top:5px;color:var(--color-gray3)}.BreadCrumb ul{margin-left:0}.BreadCrumb ul li{font-size:12px;display:inline}

.Detail{width:1184px;background-color:var(--color-white);border-radius:3px;margin:16px}.Detail .detail-info{padding:30px;margin:30px 0 0 0}.Detail .detail-info p{font-size:14px}.Detail .detail-info .title{margin:15px 0 0 0}.Detail .detail-info .title h2{font-size:24px}.Detail .detail-info .price p{font-size:46px}.Detail .detail-info .btn-comprar button{background-color:var(--color-blue);border:none;color:white;padding:15px 32px;text-align:center;text-decoration:none;display:inline-block;font-size:16px;border-radius:2px;width:220px}.Detail .section{padding:3px;margin-top:10px}.Detail .section .image-container{text-align:left}.Detail .section .image-container img{float:left;margin:0 auto}.Detail .description{margin-left:32px;color:var(--color-black)}.Detail .description p{font-size:18px}.Detail .description h2{font-size:26px}

.Footer_footer__7dIj9{padding:20px;text-align:center;background:var(--color-gray2)}

